    }
}
.btn {
    border-radius: 5px;
    border: none;
    cursor: pointer;
    display: inline-block;
    padding: 15px 10px;
    text-align: center;
    font-family: Open Sans;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
    background-color: #f6b22f;
    text-decoration: none;
    filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0,startColorstr="#FFF6B22F",endColorstr="#FFF39B24");
    background-image: url(data:image/svg+xml;base64,);
    background-size: 100%;
    background-image: linear-gradient(#f6b22f,#f39b24);
  }
  .btn:hover:not([disabled=disabled]) {
    background-color: #eaa61e;
    *zoom: 1;
    filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0,startColorstr="#FFEAA61E",endColorstr="#FFE6890E");
    background-image: url(data:image/svg+xml;base64,);
    background-size: 100%;
    background-image: linear-gradient(#eaa61e,#e6890e);
  }
  .btn-transparent {
    background: transparent;
    border: 2px solid #fff;
    padding: 13px 10px;
  }
  .btn-transparent:hover {
    background: #ffa800;
    border-color: #ffa800;
    color: #fff;
  }
  
  .btn, .btn:active, .btn:hover {
    color: #fff;
  }
  .btn-sm {
    padding: 8px 24px;
  }
#banner {
    background: linear-gradient(135deg,#39beec,#074080);
    color: #fff;
    position: relative;
    overflow: hidden;
    padding-top: 65px;
}
#banner:after {
    content: " ";
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    box-shadow: inset 0 -10px 22px -10px hsl(0deg 0% 8% / 60%);
    height: 65px;
    z-index: 5;
}
/* Titles */
#banner h1,#banner h2,#banner h3,#banner h4,#banner h5,#banner h6 {
    text-align: center;
}
#banner h1 {
    font-size: 34px;
    line-height: 1.2em;
}
#banner h2 {
    margin: 15px 0 20px;
    font-weight: 400;
}
#banner h3 {
    font-size: 14px;
    font-weight: 700;
}
/* Form */
.banner-form{
    width: 100%;
    position: relative;
    z-index: 3;
}
#banner .banner-form a {
    display: block;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 100%;
    box-sizing: border-box;
}
.banner-image .preview-image {
    margin: 60px auto -15px;
}
.banner-image .preview-image img {
    width: 100%;
}
@media screen and (min-width: 768px){
    #banner {
        padding: 70px 20px;
    }
    .banner-form{
        width: 380px;
    }
    .banner-form, .banner-image {
        float: left;
    }
    #banner h1 {
        font-size: 38px;
    }
    #banner h1, #banner h2, #banner h3, #banner h4, #banner h5, #banner h6 {
        text-align: left;
    }
    .banner-form a, .banner-form button {
        margin-bottom: 20px;
    }
    #banner .banner-image {
        width: calc(100% - 380px);
        position: relative;
    }
    .banner-image .preview-image {
        position: absolute;
        left: 20px;
        min-width: 1090px;
        margin: 0;
    }
    .banner-image .preview-image img {
        width: 100%;
        margin-left: 50px;
    }
    .banner-image .preview-image img,
    .banner-image .preview-image img {
        width: 60%;
    }
}


.section.borderless {
    border: 0;
}
.section {
    border-top: 1px solid #ddd;
    padding: 60px 0;
}
.section {
    border-top: 1px solid #ddd;
    padding: 60px 0;
}
.section .text {
    padding-top: 30px;
    max-width: 600px;
    margin: 0 auto;
}
.section .text h2 {
    font-size: 24px;
    color: #4c4c4c;
    font-weight: 700;
}
.section .video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    height: 0;
    overflow: hidden;
}
.section .video-container embed, .section .video-container iframe, .section .video-container object {
    position: absolute;
    border-radius: 7px;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
}
.section .image, .section .text {
    box-sizing: border-box;
}
.section .image img {
    width: 80%;
    margin: 0 auto;
    display: block;
    max-width: 530px;
}
.section .image .big-image {
    width: 100%;
}
@media screen and (min-width: 960px){
    .section.section-left .image, .section.section-left .text {
        float: left;
    }
    .section .image, .section .text {
        width: 50%;
    }
    .section.section-left .image img {
        margin-left: -30px;
    }
    .section .video-container embed, .section .video-container iframe, .section .video-container object {
        width: 90%;
        height: 90%;
    }
    .section .image img {
        width: 100%;
    }
    .section .text {
        text-align: left;
        padding: 30px;
    }
    .section .text h2 {
        font-size: 34px;
    }
    .section.vertical-center .image .big-image {
        max-width: 500px;
    }
}
@media screen and (min-width: 768px){
    .section.vertical-center .container {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
    }
    .section.vertical-center.section-left .image {
        order: -1;
    }
    .section.vertical-center .image {
        text-align: center;
    }
    .section.vertical-center .image img {
        max-width: 400px;
        margin: 0!important;
        display: inline-block;
    }
    .section.vertical-center.section-right .image {
        order: 1;
    }
}
